
const API_KEY = "b45bb63537f821558d1d973e2858ba57";

const getWheather = async (nameCity) => {
    const f = await fetch(`https://api.openweathermap.org/data/2.5/weather?lang=ru&q=${nameCity}&appid=${API_KEY}&units=metric`);
    return await f.json().then((json)=>{
      if (json.cod === "404") {
        return;
      }
      return json;
    });
  }

const getImage = (valuesCity) => {
    const data = valuesCity.weather[0];
    return `http://openweathermap.org/img/wn/${data.icon}@4x.png`;
}

const getMap = (valuesCity) => {
  return `https://www.google.com/maps/embed?pb=!1m14!1m12!1m3!1d914101.077742912!2d${valuesCity.coord.lon}!3d${valuesCity.coord.lat}!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!5e0!3m2!1sru!2sru!4v1653607168540!5m2!1sru!2sru`
}

export { getImage, getWheather,getMap };