<template>
  <div class="inputCity">
    <p>Введите город:</p>
    <input type="text" placeholder="Москва" v-model="nameCity" @keydown.enter="getDataWheather">
    <button type="text" @click="getDataWheather">Click</button>
  </div>
  <div v-if="this.valuesCity" class="cityValues">
    <div class="nameValues">
      <h1>{{ this.valuesCity.name }}</h1>
      <p>Температура: {{  this.valuesCity.main.temp }} °C</p>
      <p>Макс: {{ this.valuesCity.main.temp_max }} °C</p><p>Мин: {{ this.valuesCity.main.temp_min }} °C</p>
      <p>Влажность: {{ this.valuesCity.main.humidity }}% </p><p>Давление: {{ this.valuesCity.main.pressure }} рт.ст.</p>
      <p>Облачность: {{ this.valuesCity.clouds.all }}%</p>
      <p>Координаты: {{ this.valuesCity.coord.lon}} - {{ this.valuesCity.coord.lat}}</p>
      <p>Ветер - {{ this.valuesCity.wind.speed }} м/с - Порыв: {{ this.valuesCity.wind.gust }} м/c </p>
    </div>
    <div class="secondValues">
      <div>
        <img :src="imageUrl">
        <p>{{ this.valuesCity.weather[0].description.toUpperCase() }}</p>
      </div>
      <iframe :src="urlGoogle"></iframe>
    </div>
  </div>
  
</template>

<script>
import {getImage, getWheather, getMap} from "./api";


export default {
  data() {
    return {
      nameCity:"",
      valuesCity:null,
      imageUrl:"",
      urlGoogle:"",
    }
  },

  created() {
    const storage = window.localStorage.getItem("values-city");
    if (storage) {
      this.valuesCity = JSON.parse(storage);
      this.imageUrl = getImage(this.valuesCity);
      this.urlGoogle = getMap(this.valuesCity);
    }
  },

  methods: {
    async getDataWheather() {
      this.valuesCity = await getWheather(this.nameCity);
      if (this.valuesCity) {
        this.imageUrl = getImage(this.valuesCity);
        this.urlGoogle = getMap(this.valuesCity);
        this.addToLocalStorage();
      }
    },
    addToLocalStorage() {
      window.localStorage.setItem("values-city",JSON.stringify(this.valuesCity));
    },
  }
}
</script>

